<template lang="pug">
main.mainContent.orderInfoMain
  loading(:active="isLoading")
    .loadingOuter
      .load
        div
        div
        div
        div
        div
  ul.breadcrumb
    li
      router-link(to="/cart") CART \ 
  section.cartContent
    ul.progressContent
      li
        .number 01
        .text
          .en Check Order
          .cn 
      li.active
        .number 02
        .text
          .en Check OrderInfo
          .cn 
      li
        .number 03
        .text
          .en Order Complete
          .cn 
    .cartList
      .list
        .title 
        .infoTableOuter
          table.table.table-hover.listTable
            tbody
              tr(v-for="item in order.products" :key="item.id")
                td
                  img.img(:src="item.product.imageUrl" :alt="item.product.title")
                td
                  .name {{ item.product.title }}
                  .number 
                    span {{ item.qty }}
                td {{ convertToAmount(item.total) }}
        .priceOuter
          .totalPrice.finalTotal 
            span {{ convertToAmount(order.total) }}

      .formOuter
        .title 
        .orderInfo
          table.table.table-hover
            tbody
              tr
                th.orderNumText 
                td.orderNumOuter
                  p.orderNum(ref="orderNum") {{ order.id }}
                  button.btn.btn-transparent.copy(type='button' data-bs-toggle="tooltip" data-bs-placement="top" title="<em class='tool'></em>" ref="tooltips" @click="copy")
                    i.fa-solid.fa-copy
              tr
                th 
                td {{ date(order.create_at) }}
              tr
                th 
                td {{ order.user.name }}
              tr
                th 
                td {{ order.user.email }}
              tr
                th 
                td {{ order.user.tel }}
              tr
                th 
                td {{ order.user.address }}
              tr
                th 
                td {{ order.message }}
        input.payBtn(type="button" value="" @click="pay")
</template>

<script>
import Swal from 'sweetalert2'
import Tooltips from 'bootstrap/js/dist/tooltip'

export default {
  data () {
    return {
      order: {
        user: {},
        total: 0
      },
      isLoading: false,
      tooltips: ''
    }
  },
  methods: {
    // 
    copy () {
      const range = document.createRange()
      const orderNum = this.$refs.orderNum
      range.selectNode(orderNum)
      const selection = window.getSelection()
      selection.removeAllRanges()
      selection.addRange(range)
      document.execCommand('copy')
      selection.removeAllRanges()
      const tool = document.querySelector('.tool')
      tool.innerHTML = '<em></em>'
    },
    // 
    date (date) {
      const newDate = new Date(date * 1000)
      const dateTime = `${newDate.getFullYear()}/${newDate.getMonth() + 1}/${newDate.getDate()}`
      return dateTime
    },
    // $+
    convertToAmount (num) {
      const parts = num.toString().split('.')
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',')
      return '$ ' + parts.join('.')
    },
    // 
    pay () {
      const orderId = this.$route.params.id
      this.axios.post(`${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/pay/${orderId}`)
        .then(res => {
          const Toast = Swal.mixin({
            toast: true,
            position: 'top',
            showConfirmButton: false,
            timerProgressBar: true,
            timer: 1400,
            width: 220
          })
          Toast.fire({
            icon: 'success',
            title: res.data.message
          })
          setTimeout(() => {
            //  replace()
            this.$router.replace('/order-complete')
          }, 1500)
        })
        .catch(error => {
          const Toast = Swal.mixin({
            toast: true,
            position: 'top',
            showConfirmButton: false,
            timerProgressBar: true,
            timer: 1400,
            width: 320
          })
          Toast.fire({
            icon: 'error',
            title: error.response.data.message
          })
          this.isLoading = false
        })
    },
    // 
    getOrders () {
      this.isLoading = true
      const orderId = this.$route.params.id
      this.axios.get(`${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/order/${orderId}`)
        .then(res => {
          this.order = res.data.order
          this.isLoading = false
        })
        .catch(error => {
          const Toast = Swal.mixin({
            toast: true,
            position: 'top',
            showConfirmButton: false,
            timerProgressBar: true,
            timer: 1400,
            width: 320
          })
          Toast.fire({
            icon: 'error',
            title: error.response.data.message
          })
          this.isLoading = false
        })
    }
  },
  created () {
    this.getOrders()
  },
  mounted () {
    this.tooltips = new Tooltips(this.$refs.tooltips, {
      html: true
    })
  }
}
</script>
